import { useState, useEffect, useRef } from 'react';
import { Volume2, VolumeX, CloudRain, Wind, Coffee, Zap, Flame, Waves, Music } from 'lucide-react';
import { clsx } from 'clsx';
import { useTheme } from '../context/ThemeContext';
import { useLanguage } from '../context/LanguageContext';

// Ëé∑ÂèñÈü≥È¢ëÊñá‰ª∂ÁöÑÂÆåÊï¥Ë∑ØÂæÑ
const getAudioUrl = (path) => {
  // ÁßªÈô§ÂºÄÂ§¥ÁöÑÊñúÊù†ÔºàÂ¶ÇÊûúÊúâÔºâ
  const cleanPath = path.startsWith('/') ? path.slice(1) : path;
  
  // Âú®Next.js‰∏≠Ôºå‰ΩøÁî®process.env.NEXT_PUBLIC_BASE_PATHÊàñÁõ¥Êé•‰ΩøÁî®Áõ∏ÂØπË∑ØÂæÑ
  // Â¶ÇÊûúÊòØGitHub PagesÈÉ®ÁΩ≤ÔºåÈúÄË¶ÅÂä†‰∏äbasePath
  // Â¶ÇÊûúÊòØÂú®VercelÊàñÊú¨Âú∞ÂºÄÂèëÔºåÈÄöÂ∏∏ÊòØÊ†πË∑ØÂæÑ
  const basePath = process.env.NEXT_PUBLIC_BASE_PATH || '';
  
  return `${basePath}/${cleanPath}`;
};

const SOUNDS = [
  {
    id: 'rain',
    name: 'Èõ®Â£∞',
    icon: CloudRain,
    src: 'calming-rain-257596.mp3'
  },
  {
    id: 'fire',
    name: 'ÁØùÁÅ´',
    icon: Flame,
    src: 'fire-crackling-sounds-427410.mp3'
  },
  {
    id: 'cafe',
    name: 'ÂíñÂï°ÂéÖ',
    icon: Coffee,
    src: 'cafe-noise-32940.mp3'
  },
  {
    id: 'ocean',
    name: 'Êµ∑Êµ™',
    icon: Waves,
    src: 'ocean-waves-376898.mp3'
  },
  {
    id: 'white',
    name: 'ÁôΩÂô™Èü≥',
    icon: Zap,
    src: 'whitenoise-372485.mp3'
  },
  {
    id: 'uplifting',
    name: 'ËàíÁºìÈü≥',
    icon: Music,
    src: 'uplifting-pad-texture-113842.mp3'
  }
];

export function SoundPlayer() {
  const [playing, setPlaying] = useState(null);
  const [volume, setVolume] = useState(0.5);
  const [audioStatus, setAudioStatus] = useState({});
  const audioRefs = useRef({});
  const { currentTheme } = useTheme();
  const { t, language } = useLanguage();

  useEffect(() => {
    // ÂàõÂª∫Èü≥È¢ëÂÖÉÁ¥†Âπ∂Â∞ùËØïÂä†ËΩΩ
    SOUNDS.forEach(sound => {
      const audio = new Audio();
      audio.volume = volume;
      audio.loop = true;
      audio.preload = 'none'; // ‰∏çÈ¢ÑÂä†ËΩΩÔºåËäÇÁúÅËµÑÊ∫ê

      const audioSrc = getAudioUrl(sound.src);
      audio.src = audioSrc;

      // ÊµãËØïÈü≥È¢ëÊòØÂê¶ÂèØÁî®
      fetch(audioSrc, { method: 'HEAD' })
        .then(response => {
          if (response.ok) {
            console.log(`‚úÖ Èü≥È¢ëÊñá‰ª∂ÂèØÁî®: ${sound.name}`);
            setAudioStatus(prev => ({ ...prev, [sound.id]: 'available' }));
          } else {
            console.error(`‚ùå Èü≥È¢ëÊñá‰ª∂‰∏çÂèØÁî®: ${sound.name} (${response.status})`);
            setAudioStatus(prev => ({ ...prev, [sound.id]: 'missing' }));
          }
        })
        .catch(error => {
          console.error(`‚ùå ÁΩëÁªúÈîôËØØ: ${sound.name}`, error);
          setAudioStatus(prev => ({ ...prev, [sound.id]: 'error' }));
        });

      // Èü≥È¢ë‰∫ã‰ª∂ÁõëÂê¨
      audio.addEventListener('loadeddata', () => {
        console.log(`üì¶ Èü≥È¢ëÂä†ËΩΩÂÆåÊàê: ${sound.name}`);
        setAudioStatus(prev => ({ ...prev, [sound.id]: 'loaded' }));
      });

      audio.addEventListener('error', (e) => {
        console.error(`üí• Èü≥È¢ëÈîôËØØ: ${sound.name}`, e);
        setAudioStatus(prev => ({ ...prev, [sound.id]: 'error' }));
      });

      audio.addEventListener('play', () => {
        console.log(`‚ñ∂Ô∏è ÂºÄÂßãÊí≠Êîæ: ${sound.name}`);
      });

      audio.addEventListener('pause', () => {
        console.log(`‚è∏Ô∏è ÊöÇÂÅúÊí≠Êîæ: ${sound.name}`);
      });

      audioRefs.current[sound.id] = audio;
    });

    return () => {
      Object.values(audioRefs.current).forEach(audio => {
        if (audio) {
          audio.pause();
          audio.currentTime = 0;
        }
      });
    };
  }, []);

  useEffect(() => {
    Object.values(audioRefs.current).forEach(audio => {
      if (audio) audio.volume = volume;
    });
  }, [volume]);

  const toggleSound = async (id) => {
    const audio = audioRefs.current[id];
    if (!audio) {
      console.error('Èü≥È¢ëÂØπË±°‰∏çÂ≠òÂú®');
      return;
    }

    if (playing === id) {
      // ÂÅúÊ≠¢Êí≠Êîæ
      audio.pause();
      audio.currentTime = 0;
      setPlaying(null);
    } else {
      // ÂÅúÊ≠¢ÂÖ∂‰ªñÈü≥È¢ë
      if (playing) {
        const prevAudio = audioRefs.current[playing];
        if (prevAudio) {
          prevAudio.pause();
          prevAudio.currentTime = 0;
        }
      }

      // Êí≠ÊîæÊñ∞Èü≥È¢ë
      try {
        console.log(`üéØ Â∞ùËØïÊí≠Êîæ: ${SOUNDS.find(s => s.id === id)?.name}`);

        await audio.play();
        setPlaying(id);
        console.log(`‚úÖ Êí≠ÊîæÊàêÂäü: ${SOUNDS.find(s => s.id === id)?.name}`);
      } catch (error) {
        console.error('‚ùå Êí≠ÊîæÂ§±Ë¥•:', error);
        alert(`Êí≠ÊîæÂ§±Ë¥•: ${error.message}. ËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•ÊàñÈü≥È¢ëÊñá‰ª∂„ÄÇ`);
      }
    }
  };

  return (
    <div className={clsx("rounded-2xl p-6 shadow-xl h-full transition-colors flex flex-col", currentTheme.colors.card)}>
      <h2 className="text-xl font-bold mb-4 flex items-center gap-2">
        <span>üéµ</span> {t('Ambience')}
      </h2>

      <div className="grid grid-cols-2 sm:grid-cols-3 gap-3 flex-1">
        {SOUNDS.map(sound => {
            const Icon = sound.icon;
            const isPlaying = playing === sound.id;
            const status = audioStatus[sound.id];

            return (
                <button
                    key={sound.id}
                    onClick={() => toggleSound(sound.id)}
                    disabled={status === 'missing' || status === 'error'}
                    className={clsx(
                        "flex flex-col items-center justify-center p-3 rounded-xl transition-all border-2 relative",
                        isPlaying
                            ? `bg-${currentTheme.colors.primary}-600/20 border-${currentTheme.colors.primary}-500 text-${currentTheme.colors.primary}-400`
                            : "bg-black/10 border-transparent opacity-60 hover:opacity-100 hover:bg-black/20",
                        (status === 'missing' || status === 'error') && "opacity-30 cursor-not-allowed border-red-500/50",
                        status === 'available' && "border-green-500/50"
                    )}
                >
                    <Icon size={28} className="mb-2" />
                    <span className="text-xs font-medium">{language === 'zh' ? sound.name : t(sound.id)}</span>

                    {/* Áä∂ÊÄÅÊåáÁ§∫Âô® */}
                    {status === 'loading' && (
                        <div className="absolute top-1 right-1 w-2 h-2 bg-yellow-500 rounded-full animate-pulse"></div>
                    )}
                    {status === 'error' && (
                        <div className="absolute top-1 right-1 w-2 h-2 bg-red-500 rounded-full"></div>
                    )}
                    {status === 'missing' && (
                        <div className="absolute top-1 right-1 w-2 h-2 bg-gray-500 rounded-full"></div>
                    )}
                    {isPlaying && (
                        <div className="absolute bottom-1 left-1/2 transform -translate-x-1/2">
                            <div className="flex space-x-1">
                                <div className="w-1 h-1 bg-current rounded-full animate-bounce" style={{animationDelay: '0ms'}}></div>
                                <div className="w-1 h-1 bg-current rounded-full animate-bounce" style={{animationDelay: '150ms'}}></div>
                                <div className="w-1 h-1 bg-current rounded-full animate-bounce" style={{animationDelay: '300ms'}}></div>
                            </div>
                        </div>
                    )}
                </button>
            )
        })}
      </div>

      <div className="mt-6 flex items-center gap-4 bg-black/20 p-3 rounded-lg">
        <Volume2 size={20} className="opacity-60" />
        <input
            type="range"
            min="0"
            max="1"
            step="0.01"
            value={volume}
            onInput={(e) => setVolume(parseFloat(e.target.value))}
            className={clsx("w-full h-2 rounded-lg appearance-none cursor-pointer bg-black/40", `accent-${currentTheme.colors.primary}-500`)}
        />
      </div>

      <div className="mt-2 text-xs text-gray-400 text-center">
        {language === 'zh' ? 'ÁÇπÂáªÊåâÈíÆÊí≠ÊîæÁôΩÂô™Èü≥ÔºåÂÜçÊ¨°ÁÇπÂáªÂÅúÊ≠¢' : 'Click to play ambience, click again to stop'}
      </div>

      {/* ÁΩëÁªúÁä∂ÊÄÅÊèêÁ§∫ */}
      {Object.values(audioStatus).some(status => status === 'error' || status === 'missing') && (
        <div className="mt-2 text-xs text-yellow-400 text-center bg-yellow-900/20 p-2 rounded">
          {language === 'zh'
            ? '‚ö†Ô∏è ÈÉ®ÂàÜÈü≥È¢ëÂä†ËΩΩÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•'
            : '‚ö†Ô∏è Some audio files failed to load, please check network connection'
          }
        </div>
      )}
    </div>
  );
}
